<template>
  <div class="home">
    <img alt="Vue logo" src="../assets/logo.png" />
    <HelloWorld :msg="msg" />
  </div>
</template>

<script>
// @ is an alias to /src
import { ref } from "vue";
import HelloWorld from "@/components/HelloWorld.vue";
import { test } from "@/api/test";
export default {
  name: "Home",
  components: {
    HelloWorld,
  },
  setup() {
    const msg = ref("Welcome to Your Vue.js App");

    test().then((rep) => {
      msg.value = rep.data;
    });

    return {
      msg,
    };
  },
};
</script>
